.sidebar-container {
  margin-top: 0.5em;
  position: sticky;
  top: 95px;
}

//Archives side menu
.dropdown {
  outline: none;
  padding: 0;

  h4 {
    font-weight: 400;
    display: inline-block;

    &:hover {
      font-weight: 400;
    }
  }
}

.category-side {
  list-style: none;
}

.dropdown-content-side {
  display: flex;

  li {
    text-align: left;
    padding: 0.3em 1em;
   
    font-size: 0.9em;

    &:hover {
      font-weight: 400;
      cursor: pointer;
    }
    .archive-list {
      width: 150px;
      border-top: 1px solid $border-left-top;
    }
  }
}

.sidebar {
  //border: 1px solid blue;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: stretch;
  margin: 0;
  padding: 0;
}
.sidenav-header {
  font-weight: 600 !important;
}
.sidenav {
  margin-bottom: 1em;
  width: 180px;
  border-left: 1px solid $border-left-top;
  border-top: 1px solid $border-left-top;
  box-shadow: 5px 5px 5px $box-shadow;
  text-align: left;
 
  h4 {
    padding: 0.5em 1em;
  }
  ul {
    list-style: none;

    li {
      text-align: left;
      padding: 0.3em 1em;
      color: $primary-text;
      font-size: 0.9em;

      &:hover {
        font-weight: 400;
        cursor: pointer;
      }
      .article-list {
        border-top: 1px solid $border-left-top;
      }
      .article-list-date {
        font-size: 0.7em;
        color: $primary-text;
        font-weight: 300;
      }
    }
  }
  table {
    padding: 0em 0.5em;
    width: 180px;
  }
  .th-archive {
    text-align: left;
    padding: 0 0.5em;
    color: $primary-text;
    font-size: 1em;
    border-top: 1px solid $border-left-top;
  }
  .td-archive {
    text-align: left;
    padding: 0em 1em;
    font-size: 0.8em;
    &:hover {
      font-weight: 400;
      cursor: pointer;
    }
  }
}
